<template>
  <div
    class="d-flex justify-center align-center flex-grow-1 flex-sm-grow-0 full-height"
    :class="{ 'content-wrapper': !$root.isMobile }"
  >
    <div
      class="d-flex flex-column align-center white border-radius justify-space-between justify-sm-start px-5 pb-10 pa-sm-12"
      :class="{ 'box-shadow': !$root.isMobile }"
    >
      <div class="d-flex flex-column align-self-stretch">
        <h1 class="sign__in text-center mb-2">
          Sign In
        </h1>

        <p class="font-weight-normal mb-2 input-label">
          Choose your business type<span class="required-mark">*</span>
        </p>

        <div class="d-flex full-width justify-space-between mb-5">
          <button
            class="role-btn pa-3 d-flex justify-start align-center flex-column flex-sm-row"
            :class="{ active: selectedRole === $constants.ROLES.CARRIER }"
            type="button"
            @click="selectedRole = $constants.ROLES.CARRIER"
          >
            <img
              class="check"
              :src="require('@images/icons/check.svg')"
            />
            <img
              class="mr-sm-4"
              :src="require('@images/icons/carrier.svg')"
            />
            <p class="font-weight-bold">
              {{ $constants.ROLES.CARRIER | capitalize }}
            </p>
          </button>

          <button
            class="role-btn pa-3 d-flex justify-start align-center flex-column flex-sm-row"
            :class="{ active: selectedRole === $constants.ROLES.DISPATCHER }"
            type="button"
            @click="selectedRole = $constants.ROLES.DISPATCHER"
          >
            <img
              class="check"
              :src="require('@images/icons/check.svg')"
            />
            <img
              class="mr-sm-4"
              :src="require('@images/icons/dispatcher.svg')"
            />
            <p class="font-weight-bold">
              {{ $constants.ROLES.DISPATCHER | capitalize }}
            </p>
          </button>
        </div>

        <v-form
          ref="form"
          v-model="form.valid"
          class="form"
          lazy-validation
          @keyup.enter.native="onSubmit"
        >
          <base-field-input
            label="Email"
            autocomplete="user-email"
            type="email"
            placeholder="Enter Email"
            v-model="form.values.email"
            :rules="form.emailRules"
            :error="Boolean(serverErrorMessage)"
            :is-required="true"
            validate-on-blur
          />

          <base-field-input
            label="Password"
            autocomplete="user-password"
            placeholder="Enter password"
            v-model="form.values.password"
            :type="isPasswordVisible ? 'text' : 'password'"
            :rules="form.passwordRules"
            :append-icon="isPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="isPasswordVisible = !isPasswordVisible"
            :error="Boolean(serverErrorMessage)"
            :is-required="true"
            :error-messages="serverErrorMessage"
          />
        </v-form>

        <router-link
          :to="{ name: $constants.ROUTE_NAMES.FORGOT_PASSWORD, params: { role: selectedRole } }"
          class="align-self-baseline caption text-decoration-none font-weight-medium mb-7"
        >
          Forgot password?
        </router-link>
      </div>
      <div>
        <v-btn
          color="primary"
          class="text-uppercase custom__btn"
          min-width="430"
          :disabled="!form.valid"
          :loading="isLoading"
          depresse
          :block="$root.isMobile"
          large
          @click="onSubmit"
        >
          Sign In as {{ selectedRole }}
        </v-btn>
        <template v-if="$root.isMobile">
          <div class="d-flex justify-center relative divider-wrapper">
            <span class="d-flex caption divider--text white px-1 my-1 divider-text">or</span>
          </div>
        </template>
        <p class="text-uppercase font-weight-normal sign__up__text">
          <router-link
            :to="signUpRoute"
            class="text-decoration-underline cursor-pointer"
            style="color: #518ED1;"
          >
            Sign up
          </router-link>, i dont have account
        </p>
        <!-- <v-btn
          color=""
          class="text-uppercase"
          :to="signUpRoute"
          depressed
          :block="$root.isMobile"
          min-width="215"
          large
        >
          Sign Up as {{ selectedRole }}
        </v-btn> -->
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import BaseFieldInput from '@components/BaseFieldInput';
import capitalize from '@utils/filters/capitalize';
import { required, email } from '@utils/helpers/validationRules';
import { actions } from '@modules/auth/constants';

const { mapActions, mapState } = createNamespacedHelpers('auth');

export default {
  name: 'SignIn',
  components: {
    BaseFieldInput,
  },
  filters: {
    capitalize,
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.form.serverErrorMessage = '';
      },
    },
    selectedRole() {
      this.form.serverErrorMessage = '';
      this.$refs.form.resetValidation();
    },
  },
  data() {
    return {
      selectedRole: this.$constants.ROLES.CARRIER,
      isPasswordVisible: false,
      form: {
        values: {
          email: '',
          password: '',
        },
        valid: true,
        emailRules: [required('Email'), email],
        passwordRules: [required('Password')],
        serverErrorMessage: '',
      },
    };
  },
  computed: {
    signUpRoute() {
      return {
        name: this.$constants.ROUTE_NAMES[`SIGN_UP_${this.selectedRole.toUpperCase()}`],
      };
    },
    serverErrorMessage() {
      if (this.form.serverErrorMessage) {
        return this.form.serverErrorMessage;
      }

      return null;
    },
    ...mapState(['isLoading']),
  },
  methods: {
    capitalize,
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;

      this.signin({
        email: this.form.values.email,
        password: this.form.values.password,
        role: `${this.selectedRole}`,
        vm: this,
      }).catch((errorMessage) => {
        this.form.serverErrorMessage = errorMessage;
      });
    },
    ...mapActions({
      signin: actions.SIGN_IN_FORM_SUBMIT,
    }),
  },
};
</script>

